/* --- ANA SAYFA STİLLERİ --- */
.home-page .app-main {
    padding: 80px 1.5rem 100px 1.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.5rem;
}

.home-page .section-title {
    font-size: 1.2rem;
    font-weight: 700;
    color: var(--text-color);
    text-align: center;
}

/* Haftalık Seri Takibi */
.home-page .streak-tracker {
    width: 100%;
    max-width: 500px;
    margin: 0 auto;
}

.home-page .streak-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0.5rem;
}

.home-page .streak-counter {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    font-size: 1.2rem;
    font-weight: 700;
    color: var(--secondary-color);
}

.home-page .streak-counter.animate .fa-fire {
    animation: burst 0.5s ease-out;
}

.home-page .pass-container {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    margin-bottom: 1rem;
}

.home-page #use-pass-btn {
    background: none;
    border: 1px solid var(--secondary-color);
    color: var(--secondary-color);
    padding: 0.3rem 0.6rem;
    border-radius: 8px;
    cursor: pointer;
    font-size: 0.8rem;
}

.home-page #pass-count-display {
    font-weight: 700;
}

.home-page .streak-circles {
    display: flex;
    justify-content: space-between;
    gap: 0.5rem;
}

.home-page .streak-circle {
    flex: 1;
    aspect-ratio: 1 / 1;
    border-radius: 50%;
    background-color: var(--card-background);
    border: 3px solid var(--border-color);
    transition: all 0.4s ease;
    cursor: pointer;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.home-page .streak-circle span {
    font-size: 0.8rem;
    font-weight: 700;
    color: var(--light-text-color);
    opacity: 0.5;
}

.home-page .streak-circle.completed, .home-page .streak-circle.passed {
    transform: scale(1.05);
}

.home-page .streak-circle.completed span, .home-page .streak-circle.passed span {
    display: none;
}

.home-page .streak-circle.passed::before, .home-page .streak-circle.completed::before {
    font-family: 'Font Awesome 5 Free';
    font-weight: 900;
    color: white;
    font-size: 1rem;
    animation: popIn 0.3s ease-out;
}

.home-page .streak-circle.passed {
    background-color: var(--secondary-color);
    border-color: var(--secondary-color);
    box-shadow: 0 4px 15px rgba(255, 152, 0, 0.4);
}

.home-page .streak-circle.completed {
    background-color: var(--primary-color);
    border-color: var(--primary-color);
    box-shadow: 0 4px 15px rgba(76, 175, 80, 0.4);
}

.home-page .streak-circle.passed::before { content: '\f051'; } /* fa-forward */
.home-page .streak-circle.completed::before { content: '\f00c'; } /* fa-check */

/* Görev Kartı */
.home-page .daily-task-card {
    background: var(--card-background);
    border-radius: 20px;
    padding: 2rem;
    text-align: center;
    box-shadow: 0 10px 30px var(--shadow-light);
    width: 100%;
    max-width: 500px;
    margin: 0 auto;
    position: relative;
    overflow: hidden;
}

.home-page .task-passed-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 152, 0, 0.9);
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.4s ease;
    z-index: 5;
}

.home-page .task-passed-overlay.active {
    opacity: 1;
}

.home-page .task-passed-overlay i {
    font-size: 3rem;
    margin-bottom: 1rem;
}

.home-page .task-passed-overlay span {
    font-size: 1.5rem;
    font-weight: 700;
}

.home-page .task-card-content p {
    min-height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 2rem;
}

.home-page .task-buttons {
    display: flex;
    gap: 1rem;
}

.home-page #prove-task-btn {
    background: linear-gradient(45deg, var(--primary-color), #81c784);
}

/* Header, Footer, Nav */
.app-header {
    /* ... existing styles ... */
}
.app-footer {
    /* ... existing styles ... */
}
.bottom-nav {
    /* ... existing styles ... */
}

@keyframes popIn {
    from { transform: scale(0.5); opacity: 0; }
    to { transform: scale(1); opacity: 1; }
}

@keyframes burst {
    0% { transform: scale(1); }
    50% { transform: scale(1.5); }
    100% { transform: scale(1); }
}