.home-page .app-main {
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
  padding: 80px 1.5rem 100px;
  display: flex;
}

.home-page .section-title {
  color: var(--text-color);
  text-align: center;
  font-size: 1.2rem;
  font-weight: 700;
}

.home-page .streak-tracker {
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
}

.home-page .streak-header {
  justify-content: space-between;
  align-items: center;
  margin-bottom: .5rem;
  display: flex;
}

.home-page .streak-counter {
  color: var(--secondary-color);
  align-items: center;
  gap: .5rem;
  font-size: 1.2rem;
  font-weight: 700;
  display: flex;
}

.home-page .streak-counter.animate .fa-fire {
  animation: .5s ease-out burst;
}

.home-page .pass-container {
  align-items: center;
  gap: .5rem;
  margin-bottom: 1rem;
  display: flex;
}

.home-page #use-pass-btn {
  border: 1px solid var(--secondary-color);
  color: var(--secondary-color);
  cursor: pointer;
  background: none;
  border-radius: 8px;
  padding: .3rem .6rem;
  font-size: .8rem;
}

.home-page #pass-count-display {
  font-weight: 700;
}

.home-page .streak-circles {
  justify-content: space-between;
  gap: .5rem;
  display: flex;
}

.home-page .streak-circle {
  aspect-ratio: 1;
  background-color: var(--card-background);
  border: 3px solid var(--border-color);
  cursor: pointer;
  border-radius: 50%;
  flex: 1;
  justify-content: center;
  align-items: center;
  transition: all .4s;
  display: flex;
  position: relative;
}

.home-page .streak-circle span {
  color: var(--light-text-color);
  opacity: .5;
  font-size: .8rem;
  font-weight: 700;
}

.home-page .streak-circle.completed, .home-page .streak-circle.passed {
  transform: scale(1.05);
}

.home-page .streak-circle.completed span, .home-page .streak-circle.passed span {
  display: none;
}

.home-page .streak-circle.passed:before, .home-page .streak-circle.completed:before {
  color: #fff;
  font-family: "Font Awesome 5 Free";
  font-size: 1rem;
  font-weight: 900;
  animation: .3s ease-out popIn;
}

.home-page .streak-circle.passed {
  background-color: var(--secondary-color);
  border-color: var(--secondary-color);
  box-shadow: 0 4px 15px #ff980066;
}

.home-page .streak-circle.completed {
  background-color: var(--primary-color);
  border-color: var(--primary-color);
  box-shadow: 0 4px 15px #4caf5066;
}

.home-page .streak-circle.passed:before {
  content: "";
}

.home-page .streak-circle.completed:before {
  content: "";
}

.home-page .daily-task-card {
  background: var(--card-background);
  text-align: center;
  box-shadow: 0 10px 30px var(--shadow-light);
  border-radius: 20px;
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
  padding: 2rem;
  position: relative;
  overflow: hidden;
}

.home-page .task-passed-overlay {
  color: #fff;
  opacity: 0;
  pointer-events: none;
  z-index: 5;
  background-color: #ff9800e6;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  transition: opacity .4s;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.home-page .task-passed-overlay.active {
  opacity: 1;
}

.home-page .task-passed-overlay i {
  margin-bottom: 1rem;
  font-size: 3rem;
}

.home-page .task-passed-overlay span {
  font-size: 1.5rem;
  font-weight: 700;
}

.home-page .task-card-content p {
  justify-content: center;
  align-items: center;
  min-height: 80px;
  margin-bottom: 2rem;
  display: flex;
}

.home-page .task-buttons {
  gap: 1rem;
  display: flex;
}

.home-page #prove-task-btn {
  background: linear-gradient(45deg, var(--primary-color), #81c784);
}

@keyframes popIn {
  from {
    opacity: 0;
    transform: scale(.5);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes burst {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.5);
  }

  100% {
    transform: scale(1);
  }
}
